import { NextResponse } from "next/server"

export async function POST(request: Request) {
    const body = await request.json()
    const { email, password } = body

    // Mock validation - accept any valid email with password "demo"
    // or just general "demo" / "demo"
    if (email && password) {
        // In a real app, verify credentials here

        // Create the response
        const response = NextResponse.json(
            { success: true, user: { email, name: "Dr. User" } },
            { status: 200 }
        )

        // Set a mock session cookie
        // Expire in 1 day
        const oneDay = 24 * 60 * 60 * 1000
        const role = body.role || "hospital" // Default to hospital if not provided

        // Store role in the token value (simplified for demo) or a separate claim
        // For this demo, we'll just store a simple JSON string to easily parse client-side if needed, 
        // or just rely on cookie presence. Let's make the cookie value richer.
        const tokenPayload = JSON.stringify({ role, email, timestamp: Date.now() });

        response.cookies.set("auth-token", Buffer.from(tokenPayload).toString('base64'), {
            httpOnly: false, // Allow client to read for UI logic (e.g. knowing which dashboard to go to)
            secure: process.env.NODE_ENV === "production",
            sameSite: "strict",
            maxAge: oneDay,
            path: "/",
        })

        return response
    }

    return NextResponse.json(
        { success: false, error: "Invalid credentials" },
        { status: 401 }
    )
}
